import React, {Component} from 'react';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import * as baseActions from 'store/modules/base';
import * as postActions from 'store/modules/post';
import AskRemoveModal from 'components/modal/AskRemoveModal';
import { withRouter } from 'react-router-dom';
class AskRemoveModalContainer extends Component {
    handleCancel = () => {
        const { BaseActions } = this.props;
        BaseActions.hideModal('remove');
    }

    handleConfirm = async () => {
        const { PostActions, match, BaseActions, history } = this.props;
        const { id } = match.params;

        try {
            await PostActions.removePost({id});
            BaseActions.hideModal('remove');
            history.push('/');
        } catch(e){
            console.error(e);
        }
    }
    render() {
        const {visible} = this.props;
        const {handleCancel, handleConfirm} = this;
        return (<AskRemoveModal visible={visible} onCancel={handleCancel} onConfirm={handleConfirm}/>);
    }
}
export default connect((state) => ({
    visible: state
        .base
        .getIn(['modal', 'remove'])
}), (dispatch) => ({
    BaseActions: bindActionCreators(baseActions, dispatch),
    PostActions: bindActionCreators(postActions, dispatch)
}))(withRouter(AskRemoveModalContainer));


